<h2>Instance Properties</h2>
<h3><code v-pre>vc-geometry-box</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>center</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> The circle's center point in the fixed frame. <strong>Structure: { lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>radius</td>
<td>Number</td>
<td></td>
<td><code v-pre>required</code> The radius in meters.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The ellipsoid the circle will be on.</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> The distance in meters between the circle and the ellipsoid surface.</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td><code v-pre>0.02</code></td>
<td><code v-pre>optional</code> The angular distance between points on the circle in radians.</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The vertex attributes to be computed.</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> The distance in meters between the circle's extruded face and the ellipsoid surface.</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> The rotation of the texture coordinates, in radians. A positive rotation is counter-clockwise.</td>
</tr>
</tbody>
</table>
